.video-options {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding-top: 50px
}

.vid-card {
    color: #fff;
    padding-top: 20px;
}

.vid-card:hover {
    background-color: #fff;
    color: #000;
}

.vid-image {
    width: 200px;
    margin: 0 20px;
}

.vid-title {
    font-size: 16x;
    text-align: center;
}

@media screen and (max-width: 1000px) {
    .vid-image {
        width: 85px;
        margin: 0 5px;
    }

    .vid-title {
        display: none;
    }
}
#contact {
    background-color: #000;
    height: 95vh;
    padding: 60px 0;
    /* text-align: center; */
}

.contact-form {
    display: flex;
    justify-content: space-evenly;
}

form {
    width: 50%;
    padding: 40px;
}

.social-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
}

.form-item {
    margin-bottom: 20px;

}

input {
    width: 100%;
    height: 30px;
    font-family: 'Roboto', sans-serif;
}

textarea {
    width: 100%;
    height: 200px;
    font-family: 'Roboto', sans-serif;
}

button {
    font-family: 'Bakbak One', cursive !important;
    font-size: 16px !important;
    background-color: #F8CE27;
    height: 30px;
    width: 100px;
}

.social-icon {
    font-size: 50px;
    line-height: 50px;
    color: #F8CE27;
    text-align: center;
    cursor: pointer;
}


@media screen and (max-width: 1000px) {
    .contact-form img {
        display: none;
    }

    .contact-form {
        display: block;
        padding: 20px;
    }

    .social-container {
        justify-content: space-evenly;
    }

    form {
        width: 100%;
        padding: 0;
    }

    input {
        width: 100%;
    }

    textarea {
        width: 100%;
    }
}
